.header {
  background-color: white;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.header__items {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  font-size: 1rem;
  margin-right: 2rem;
}
.header__list {
  margin-left: 5rem;
  stroke: none;
  text-decoration: none;
  cursor: pointer;
  font-size: 1.3rem;
  font-weight: 300;
  background-color: #ffffff;
}

.header__logo {
  font-family: 'Mr De Haviland', cursive;
  margin-left: 1rem;
  font-size: 2.5rem;
  font-weight: 600;
  cursor: pointer;
  stroke: none;
  color: #2f2e41;
  text-decoration: none;
}
.header__logo:hover {
  color: #2f2e41;
}

.header a {
  cursor: pointer;
  stroke: none;
  text-decoration: none;
  margin-top: 2rem;
  color: #2f2e41;
}
.header a:hover {
  color: #407bff;
}

@media (max-width: 960px) {
  .header__items {
    display: none;
  }
}

